import { VehicleCard } from '@/components/VehicleCard';
import { mockVehicles } from '@/data/mockData';
import { GlassCard } from '@/components/ui/GlassCard';
import { Reveal } from '@/components/ui/Reveal';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import {
  Activity,
  AlertTriangle,
  Car,
  Ambulance,
  Helicopter,
  TrendingUp,
  Wrench,
  Bell,
  Clock,
  ArrowRight,
  ShieldCheck,
  Zap
} from 'lucide-react';
import Link from 'next/link';

export default function Home() {
  // Calculate dashboard statistics
  const totalVehicles = mockVehicles.length;
  const activeVehicles = mockVehicles.filter(v => v.status === 'active').length;
  const maintenanceVehicles = mockVehicles.filter(v => v.status === 'maintenance').length;
  const outOfServiceVehicles = mockVehicles.filter(v => v.status === 'out_of_service').length;

  const totalDevices = mockVehicles.reduce((acc, vehicle) => acc + vehicle.devices.length, 0);
  const overdueDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'overdue').length, 0
  );
  const dueSoonDevices = mockVehicles.reduce((acc, vehicle) =>
    acc + vehicle.devices.filter(d => d.status === 'due').length, 0
  );

  const cars = mockVehicles.filter(v => v.type === 'car');
  const ambulances = mockVehicles.filter(v => v.type === 'ambulance');
  const helicopters = mockVehicles.filter(v => v.type === 'helicopter');

  const priorityDevices = mockVehicles.flatMap(vehicle =>
    vehicle.devices.map(device => ({
      ...device,
      vehicleName: vehicle.name,
      vehicleId: vehicle.id,
      vehicleType: vehicle.type
    }))
  )
    .filter(device => device.status === 'overdue' || device.status === 'due')
    .sort((a, b) => {
      const statusPriority: Record<string, number> = {
        overdue: 0,
        due: 1,
        upcoming: 2,
        completed: 3
      };
      return (statusPriority[a.status] ?? 3) - (statusPriority[b.status] ?? 3);
    })
    .slice(0, 6);

  return (
    <div className="min-h-screen bg-black text-gray-50 overflow-x-hidden selection:bg-blue-500/30">

      {/* Hero Section */}
      <section className="relative h-[90vh] flex flex-col items-center justify-center overflow-hidden">
        {/* Abstract Background Gradient */}
        <div className="absolute inset-0 bg-[radial-gradient(circle_at_50%_50%,_rgba(0,113,227,0.15),_transparent_70%)] animate-pulse duration-[5000ms]" />

        <Reveal>
          <div className="text-center relative z-10 space-y-6 max-w-4xl px-6">
            <Badge variant="outline" className="mb-4 border-white/20 text-blue-400 bg-blue-500/10 backdrop-blur-md px-4 py-1.5 rounded-full text-sm">
              <SparklesIcon className="w-3 h-3 mr-2 inline" />
              Hamad Medical Corporation
            </Badge>
            <h1 className="text-5xl md:text-7xl lg:text-8xl font-semibold tracking-tighter bg-clip-text text-transparent bg-gradient-to-b from-white to-white/60">
              Fleet Commander.
            </h1>
            <p className="text-xl md:text-2xl text-gray-400 font-light max-w-2xl mx-auto leading-relaxed">
              The advanced ecosystem for tracking vehicles, equipment, and maintenance lifecycles in real-time.
            </p>

            <div className="flex items-center justify-center gap-4 pt-4">
              <Link href="/vehicles">
                <Button size="lg" className="rounded-full px-8 h-12 text-base bg-white text-black hover:bg-gray-200 transition-colors">
                  View Fleet
                </Button>
              </Link>
              <Link href="/reports">
                <Button size="lg" variant="outline" className="rounded-full px-8 h-12 text-base border-white/20 text-white hover:bg-white/10 backdrop-blur-md">
                  View Reports
                </Button>
              </Link>
            </div>
          </div>
        </Reveal>

        <div className="absolute bottom-10 animate-bounce text-gray-500">
          <ArrowRight className="h-6 w-6 rotate-90" />
        </div>
      </section>

      <main className="max-w-[1400px] mx-auto px-6 py-24 space-y-32">

        {/* HUD Stats Section */}
        <Reveal width="100%">
          <section className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
            <StatsCard
              title="Total Vehicles"
              value={totalVehicles}
              subtext={`${cars.length} Cars, ${ambulances.length} Ambulances`}
              icon={<Car className="h-5 w-5 text-blue-400" />}
            />
            <StatsCard
              title="Active Fleet"
              value={activeVehicles}
              subtext={`${maintenanceVehicles} Maintenance, ${outOfServiceVehicles} Offline`}
              icon={<Activity className="h-5 w-5 text-emerald-400" />}
            />
            <StatsCard
              title="Total Devices"
              value={totalDevices}
              subtext="Monitored 24/7"
              icon={<Zap className="h-5 w-5 text-amber-400" />}
            />
            <StatsCard
              title="Attention Needed"
              value={overdueDevices + dueSoonDevices}
              subtext="Critical Maintenance Items"
              icon={<AlertTriangle className="h-5 w-5 text-red-400" />}
              highlight
            />
          </section>
        </Reveal>

        {/* Priority Action Section with Parallax Feel */}
        <section className="relative">
          <div className="flex items-center justify-between mb-12">
            <Reveal>
              <h2 className="text-4xl font-semibold tracking-tight">Priority Maintenance</h2>
            </Reveal>
            <Reveal delay={0.4}>
              <Link href="/notifications" className="text-blue-400 hover:text-blue-300 flex items-center gap-2 group transition-colors">
                All Systems <ArrowRight className="h-4 w-4 group-hover:translate-x-1 transition-transform" />
              </Link>
            </Reveal>
          </div>

          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {priorityDevices.length > 0 ? (
              priorityDevices.map((device, i) => (
                <Reveal key={device.id} delay={i * 0.1}>
                  <Link href={`/vehicle/${device.vehicleId}`}>
                    <GlassCard hoverEffect className="h-full group border-l-4 border-l-red-500/50">
                      <div className="flex justify-between items-start mb-4">
                        <Badge variant="secondary" className="bg-red-500/10 text-red-400 border-red-500/20">
                          {device.status === 'overdue' ? 'Overdue' : 'Due Soon'}
                        </Badge>
                        <Wrench className="h-5 w-5 text-gray-500 group-hover:text-white transition-colors" />
                      </div>
                      <h3 className="text-xl font-bold mb-1 group-hover:text-blue-400 transition-colors">{device.name}</h3>
                      <p className="text-sm text-gray-400 mb-4">{device.vehicleName}</p>

                      <div className="flex items-center gap-3 text-sm text-gray-300 bg-white/5 p-3 rounded-xl">
                        <Clock className="h-4 w-4 text-red-400" />
                        <span>Due: {device.nextMaintenanceDate.toLocaleDateString()}</span>
                      </div>
                    </GlassCard>
                  </Link>
                </Reveal>
              ))
            ) : (
              <Reveal width="100%">
                <GlassCard className="p-12 text-center text-gray-400">
                  <ShieldCheck className="h-16 w-16 mx-auto mb-4 text-emerald-500/50" />
                  <h3 className="text-xl font-semibold text-white mb-2">All Systems Operational</h3>
                  <p>No critical maintenance items pending.</p>
                </GlassCard>
              </Reveal>
            )}
          </div>
        </section>

        {/* Fleet Showcase Grid */}
        <section>
          <Reveal>
            <h2 className="text-4xl font-semibold tracking-tight mb-12">Fleet Overview</h2>
          </Reveal>

          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-8">
            {/* Show a mix of vehicles, limited for the home page */}
            {[...cars.slice(0, 3), ...ambulances.slice(0, 2), ...helicopters.slice(0, 1)].map((vehicle, i) => (
              <VehicleCard key={vehicle.id} vehicle={vehicle} />
            ))}
          </div>

          <div className="mt-12 text-center">
            <Link href="/vehicles">
              <Button variant="outline" size="lg" className="rounded-full px-8 border-white/10 text-gray-300 hover:text-white hover:bg-white/5">
                View All {totalVehicles} Vehicles
              </Button>
            </Link>
          </div>
        </section>

      </main>
    </div>
  );
}

function StatsCard({ title, value, subtext, icon, highlight = false }: any) {
  return (
    <GlassCard className={`relative overflow-hidden ${highlight ? 'bg-red-500/5 border-red-500/20' : ''}`}>
      <div className="absolute top-0 right-0 p-4 opacity-50">
        {icon}
      </div>
      <div className="space-y-2 relative z-10">
        <p className="text-sm font-medium text-gray-400 uppercase tracking-wider">{title}</p>
        <div className="text-4xl font-bold text-white tracking-tight">{value}</div>
        <p className="text-xs text-gray-500">{subtext}</p>
      </div>
      {highlight && (
        <div className="absolute inset-0 bg-gradient-to-br from-red-500/10 to-transparent pointer-events-none" />
      )}
    </GlassCard>
  )
}

function SparklesIcon(props: any) {
  return (
    <svg
      {...props}
      xmlns="http://www.w3.org/2000/svg"
      width="24"
      height="24"
      viewBox="0 0 24 24"
      fill="none"
      stroke="currentColor"
      strokeWidth="2"
      strokeLinecap="round"
      strokeLinejoin="round"
    >
      <path d="m12 3-1.912 5.813a2 2 0 0 1-1.275 1.275L3 12l5.813 1.912a2 2 0 0 1 1.275 1.275L12 21l1.912-5.813a2 2 0 0 1 1.275-1.275L21 12l-5.813-1.912a2 2 0 0 1-1.275-1.275L12 3Z" />
    </svg>
  )
}
